.leaderboard-table {
  width: 100%;
  display: flex;
  justify-content: center; /* center table within container */
}

/* Base table style pulls from app theme but refined for readability */
.leaderboard {
  width: 100%;
  max-width: 900px; /* wider, matches history card width */
  border-collapse: separate;
  border-spacing: 0;
  border-radius: 8px; /* match graph radius */
  overflow: hidden;
  background: rgba(40,40,40,0.18); /* subtle like graph */
  border: 1px solid rgba(255,255,255,0.1);
  backdrop-filter: blur(6px);
}

.light .leaderboard {
  background: rgba(255,255,255,0.18);
  border: 1px solid rgba(0,0,0,0.1);
  backdrop-filter: blur(6px);
}

/* Header improvements: stronger gradient and tighter paddings on small screens */
.leaderboard th {
  background: linear-gradient(90deg, var(--table-header-start), var(--table-header-end));
  color: white;
  padding: 10px 12px; /* tighter */
  text-align: center;
  font-size: 13px; /* slightly smaller on desktop */
  letter-spacing: 0.2px;
}

/* Column sizing for better layout */
.col-rank { width: 64px; text-align: center; }
.col-player { text-align: left; }
.col-resources, .col-last, .col-diff { width: 22%; }

/* Cells */
.leaderboard td {
  padding: 8px 8px; /* tighter to allow more text */
  text-align: center;
  font-size: 13px; /* slightly smaller on desktop */
}

.leaderboard td.col-player { text-align: left; }

.leaderboard tr:nth-child(even) { background: rgba(255,255,255,0.08); }
.light .leaderboard tr:nth-child(even) { background: rgba(0,0,0,0.06); }

/* Remove hover background to keep table clean */
/* .leaderboard tr:hover and light variant intentionally disabled */

/* Difference coloring from app */
.waiting { color: #555; }
.positive { color: var(--table-header-start); font-weight: bold; }
.dark .positive { color: var(--diff-green); }

/* Arrow badges spacing in player column */
.col-player .arrow { margin-right: 6px; }

/* Responsive tweaks */
@media (max-width: 700px) {
  .leaderboard { max-width: 100%; }
  .col-resources, .col-last, .col-diff { width: auto; }
  .col-rank { width: 48px; }
  .leaderboard th { padding: 7px 7px; font-size: 11px; }
  .leaderboard td { padding: 6px 6px; font-size: 10px; }
}

@media (max-width: 450px) {
  .leaderboard th { padding: 5px 6px; font-size: 9px; }
  .leaderboard td { padding: 4px 5px; font-size: 8px; }
  .col-rank { width: 40px; }
}
